'use client';

import { Settings, Shield, Bell, Database, Globe } from 'lucide-react';

export default function SettingsPage() {
    return (
        <div style={{ maxWidth: '800px', margin: '0 auto' }}>
            <header style={{ marginBottom: '2rem' }}>
                <h1 style={{ fontSize: '1.8rem', fontWeight: 'bold', color: 'var(--primary)' }}>الإعدادات</h1>
                <p style={{ color: 'var(--text-muted)' }}>تخصيص النظام وإدارة الأمان والتنبيهات</p>
            </header>

            <div style={{ display: 'flex', flexDirection: 'column', gap: '1.5rem' }}>
                <section className="card">
                    <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '1.5rem' }}>
                        <div style={{ padding: '0.75rem', background: 'rgba(30, 58, 138, 0.1)', color: 'var(--primary)', borderRadius: '0.5rem' }}>
                            <Globe size={20} />
                        </div>
                        <h3 style={{ fontWeight: 'bold' }}>إعدادات المدرسة</h3>
                    </div>
                    <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '1.5rem' }}>
                        <div style={{ gridColumn: 'span 2' }}>
                            <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '500' }}>اسم المدرسة</label>
                            <input type="text" className="input-field" defaultValue="مدرسة المستشار أحمد عصمت" />
                        </div>
                        <div>
                            <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '500' }}>العام الدراسي الحالي</label>
                            <input type="text" className="input-field" defaultValue="2025/2026" />
                        </div>
                        <div>
                            <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '500' }}>المنطقة التعليمية</label>
                            <input type="text" className="input-field" defaultValue="الغربية - بسيون" />
                        </div>
                    </div>
                </section>

                <section className="card">
                    <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '1.5rem' }}>
                        <div style={{ padding: '0.75rem', background: 'rgba(16, 185, 129, 0.1)', color: 'var(--success)', borderRadius: '0.5rem' }}>
                            <Shield size={20} />
                        </div>
                        <h3 style={{ fontWeight: 'bold' }}>الأمان والخصوصية</h3>
                    </div>
                    <div style={{ display: 'flex', flexDirection: 'column', gap: '1rem' }}>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', paddingBottom: '0.5rem', borderBottom: '1px solid var(--border)' }}>
                            <div>
                                <p style={{ fontWeight: '500' }}>المصادقة الثنائية (2FA)</p>
                                <p style={{ fontSize: '0.8rem', color: 'var(--text-muted)' }}>تأمين إضافي لحسابات الإدارة</p>
                            </div>
                            <input type="checkbox" style={{ width: '20px', height: '20px' }} />
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <div>
                                <p style={{ fontWeight: '500' }}>تسجيل عمليات الحذف</p>
                                <p style={{ fontSize: '0.8rem', color: 'var(--text-muted)' }}>تتبع كافة عمليات حذف بيانات الطلاب</p>
                            </div>
                            <input type="checkbox" defaultChecked style={{ width: '20px', height: '20px' }} />
                        </div>
                    </div>
                </section>

                <section className="card">
                    <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', marginBottom: '1.5rem' }}>
                        <div style={{ padding: '0.75rem', background: 'rgba(245, 158, 11, 0.1)', color: 'var(--accent)', borderRadius: '0.5rem' }}>
                            <Database size={20} />
                        </div>
                        <h3 style={{ fontWeight: 'bold' }}>قاعدة البيانات والنسخ الاحتياطي</h3>
                    </div>
                    <div style={{ display: 'flex', gap: '1rem' }}>
                        <button className="btn-primary" style={{ flex: 1, background: '#10b981' }}>إجراء نسخة احتياطية الآن</button>
                        <button className="btn-primary" style={{ flex: 1, background: 'var(--primary)' }}>تحميل كافة البيانات (Excel)</button>
                    </div>
                </section>
            </div>
        </div>
    );
}
